        // 设置完整的返回数据
        Vue.prototype.formatFullData = function (response) {
            var list = [];
            var total = 0;
            var result = response.body;
            if (result == null) {
                list = [];
                total = 0;
                return {
                    list: list,
                    total: total
                };
            }
            if (!result.success) {
                list = [];
                total = 0;
                return {
                    list: list,
                    total: total
                };
            }
            if (result.data == null) {
                list = [];
                total = 0;
                return {
                    list: list,
                    total: total
                };
            }
            if (result.data.content.length == 0) {
                list = [];
                total = 0;
                return {
                    list: list,
                    total: total
                };
            }
            // 设置参数
            list = result.data.content;
            total = result.data.totalElements;
            return {
                list: list,
                total: total
            };
        };
        /**
         * 标准化的获取表格的数据的方法
         * @param url 地址
         * @param data 提交表单的数据
         * @param list 要赋值的列表
         * @param total 要赋值的总数
         */
        Vue.prototype.standTableAction = function (url, data, callback) {
            this.$http.post(url, data).then(response => {
                var result = this.formatFullData(response);
                callback(result.total, result.list);
            }, response => {
                callback(0, []);
            });
        };
        /**
         * 标准的需要返回数据的post
         * @param url
         * @param data
         * @param loadingMsg
         * @param successMsg
         * @param errorMsg
         * @param callBack
         */
        Vue.prototype.standPostActionWithData = function (url, data, callBack) {
            this.$http.post(url, data).then(response => {
                var result = response.body;
                if (result == null) {
                    callBack(false, null, '请求错误,请稍后重试');
                    return;
                }
                if (!result.success) {
                    callBack(false, null, result.errorMsg, result.errorCode);
                    return;
                }
                callBack(true, result.data, null, 100);
            }, response => {
                callBack(false, null, '请求错误,请稍后重试', 101);
            });
        };
        // 显示成功
        Vue.prototype.showSuccess = function (msg) {
            try {
                this.$Message.destroy();
            } catch (e) {
            }
            this.$message({
                showClose: true,
                message: msg,
                type: 'success'
            });
        };
        /**
         * 标准的只需要post结果和错误信息的post请求
         * @param url
         * @param data
         * @param loadingMsg
         * @param successMsg
         * @param callBack
         */
        Vue.prototype.standPostActionOnlyState = function (url, data, loadingMsg, successMsg, callBack) {
            if (!this.strError(loadingMsg)) {
                this.showInfo(loadingMsg);
            }
            this.$http.post(url, data).then(response => {
                var success = this.formatSuccess(response);
                if (success == 'success') {
                    if (!this.strError(successMsg)) {
                        this.showSuccess(successMsg);
                    }
                } else {
                    if (!this.strError(success)) {
                        this.showError(success);
                    }
                }
                callBack();
            }, response => {
                this.showError('操作失败,请稍后重试');
                callBack();
            });
        };
        // 显示失败
        Vue.prototype.showError = function (msg) {
            try {
                this.$Message.destroy();
            } catch (e) {
            }
            if (msg == null || msg == 'null') {
                return;
            }
            this.$message({
                showClose: true,
                message: msg,
                type: 'error'
            });
        };
        // 设置返回是否成功
        Vue.prototype.formatSuccess = function (response) {
            var result = response.body;
            if (result == null) {
                return '出错了';
            }
            if (!result.success) {
                return result.errorMsg;
            }
            return 'success';
        };
