        // 检查表单是否保存了
        Vue.prototype.checkFormSave = function (callBack) {
            if (this.$store.state.formChanged) {
                // 提示保存
                var that = this;
                this.$Modal.confirm({
                    title: '警告',
                    onOk: function () {
                        that.$store.commit('refreshMenuName', '');
                        that.$store.commit('refreshMenuName', that.getSelectMenuName());
                    },
                    onCancel: function () {
                        that.setFormUnChange();
                        // 表单没有变化
                        that.$nextTick(function () {
                            callBack();
                        });
                    },
                    content: '<h5>修改后的表单未保存,未保存跳转将会丢失已填写数据,是否返回保存数据?</h5>',
                    okText: '留在当前页面',
                    cancelText: '直接跳转',
                });
                return true;
            }
            return false;
        };
