<template>
    <div style="position: relative;width: 100%;height: 100%;overflow: hidden">
        <!--        左侧菜单-->
        <el-menu
                id="menu_container"
                style="text-align: left"
                class="left_menu"
                :default-active="menuIndex"
                background-color="#545c64"
                text-color="#fff"
                active-text-color="#ffd04b">
            <el-menu-item index="新建清单" v-if="!isNewMenu" @click.native="isNewMenu=true">
                <i class="el-icon-plus"></i>
                <span slot="title">新建清单</span>
            </el-menu-item>
            <el-menu-item index="新建菜单" v-if="isNewMenu">
                <el-input @blur="isNewMenu = false" ref="menu_inputer" @keyup.native.enter="createMenu"
                          v-model="menuFormData.name"
                          hint="请输入清单名称"></el-input>
            </el-menu-item>
            <el-menu-item
                    :index="bean.objectId" v-for="(bean,index) in menus" @click.native="handleClickMenu(bean)"
                    @click.right.prevent.native="handleMenuRightClick(bean)"
            >
                <i class="el-icon-document"></i>
                <span slot="title">{{bean.name}}</span>
            </el-menu-item>
        </el-menu>
        <!--        列表-->
        <div class="todo_list">
            <div style="margin: 10px;">
                <div class="flex">
                    <el-input clearable style="margin-bottom: 10px;width: 300px" v-model="todoFormData.title"
                              @keyup.native.enter="createTodo"></el-input>
                    <el-checkbox v-model="onlyShowDone" class="left_10" style="margin: auto 10px;color: white">只显示未完成</el-checkbox>
                </div>
                <ElDivider></ElDivider>
            </div>
            <div style="margin: 10px;" v-for="(bean,index) in todoList">
                <div class="flex">
                    <!--勾选框-->
                    <el-checkbox style="margin: auto 10px" v-model="bean.done"
                                 @change="onMissionChanged(bean)"></el-checkbox>
                    <p style="color: white;margin: auto 10px;cursor: pointer;font-size: 16px" @click="showDetail(bean)"
                       :class="{delete_line:bean.done}">{{bean.title}}<i
                            class="el-icon-info"
                            v-if="!strError(bean.desc)"
                            style="margin-left: 10px;cursor:pointer "></i></p>
                </div>
                <HorSplit></HorSplit>
            </div>
        </div>
        <!--        详情弹窗-->
        <Modal v-model="showDetailModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>任务详情</span>
            </p>
            <div style="text-align:center">
                <el-input v-model="currentTodo.title"></el-input>
                <el-input style="margin-top: 15px" type="textarea"
                          :rows="2" v-model="currentTodo.desc"></el-input>
            </div>
            <div slot="footer">
                <Button type="success" size="large" style="margin: auto" @click="saveDetail" long>保存</Button>
                <Button type="error" size="large" style="margin: auto;margin-top: 10px" @click="handleDeleteTodo" long>删除
                </Button>
            </div>
        </Modal>
        <!--        登录弹窗-->
        <Modal v-model="showLoginModal" width="360" :closable="false" :mask-closable="false">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>登录</span>
            </p>
            <div style="text-align:center">
                <el-input v-model="loginFormData.userName"></el-input>
                <el-input style="margin-top: 15px"
                          :rows="2" v-model="loginFormData.passWord"></el-input>
            </div>
            <div slot="footer">
                <Button type="success" size="large" @click="login" long>登录</Button>
            </div>
        </Modal>
        <!--        菜单的右键菜单-->
        <Modal v-model="showMenuRightModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>菜单修改</span>
            </p>
            <div style="text-align:center">
                <el-input
                        v-model="menuFormData.name"
                >

                </el-input>

            </div>
            <div slot="footer">
                <Button type="error" size="large" style="margin: auto" long @click="showDeleteMenu">删除</Button>
                <Button type="success" class="top_10" size="large" style="margin: auto;margin-top: 10px" long
                        @click="saveMenu">保存
                </Button>
            </div>
        </Modal>
        <!--        删除菜单确认-->
        <Modal v-model="showMenuDeleteModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>是否删除选中的清单?</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" style="margin: auto" long @click="delMenu">删除</Button>
            </div>
        </Modal>
        <!--        删除Todo确认-->
        <Modal v-model="showTodoDeleteModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>是否删除选中的任务?</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" style="margin: auto" long @click="delTodo">删除</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    import HorSplit from "@/views/HorSplit";

    export default {
        name: "Main",
        components: {HorSplit},
        data() {
            return {
                // 只显示未完成
                onlyShowDone:false,
                // 删除todo确认
                showTodoDeleteModal:false,
                // 删除菜单确认
                showMenuDeleteModal: false,
                // 显示右键菜单
                showMenuRightModal: false,
                // 登录表单
                loginFormData: {
                    userName: '',
                    passWord: ''
                },
                // 显示登录modal
                showLoginModal: false,
                // 当前todo
                currentTodo: {},
                // 详情modal
                showDetailModal: false,
                // todo列表
                todoList: [],
                // todo的表单
                todoFormData: {
                    title: '',
                    desc: ''
                },
                // 选中的菜单
                currentMenu: null,
                // 菜单列表
                menus: [],
                // 创建菜单的form
                menuFormData: {
                    name: ''
                },
                // 是否是新建清单
                isNewMenu: false,
            }
        },
        watch: {
            isNewMenu: function () {
                if (this.isNewMenu) {
                    this.$nextTick(function () {
                        // 异步操作
                        this.$refs.menu_inputer.focus();
                    });
                }
            },
            onlyShowDone:function () {
                this.getTodoList();
            }
        },
        created: function () {
            // 查看是否登录了
            //获取用户当前信息
            let current = Bmob.User.current()
            if (current == null) {
                this.showError('请先登录');
                this.showLoginModal = true;
                return;
            }
            this.refresh();
            document.getElementById("menu_container").oncontextmenu = function (e) {
                return false;
            }
        },
        computed: {
            // 当前选中的菜单值
            menuIndex: function () {
                if (this.currentMenu == null) {
                    return '';
                }
                return this.currentMenu.objectId;
            }
        },
        methods: {
            // 删除todo
            delTodo: function () {
                this.showTodoDeleteModal = false;
                const query = Bmob.Query('TodoBean');
                var that = this;
                query.destroy(this.currentTodo.objectId).then(res => {
                    that.getTodoList();
                }).catch(err => {
                    that.getTodoList();
                })
            },
            // 点击删除todo
            handleDeleteTodo: function () {
                this.showDetailModal = false;
                this.showTodoDeleteModal = true;
            },
            // 更新菜单的内容
            saveMenu: function () {
                this.showMenuRightModal = false;
                const query = Bmob.Query('TodoMenuBean');
                var that = this;
                query.set('id', this.menuFormData.objectId); //需要修改的objectId
                query.set('name', this.menuFormData.name);
                query.save().then(res => {
                    that.refresh();
                }).catch(err => {
                    that.refresh();
                })
            },
            // 显示删除菜单
            showDeleteMenu: function () {
                this.showMenuRightModal = false;
                this.showMenuDeleteModal = true;
            },
            // 删除菜单
            delMenu: function () {
                this.showMenuDeleteModal = false;
                const query = Bmob.Query('TodoMenuBean');
                var that = this;
                query.destroy(this.menuFormData.objectId).then(res => {
                    that.refresh();
                }).catch(err => {
                    that.refresh();
                })
            },
            // 菜单右键点击事件
            handleMenuRightClick: function (bean) {
                this.showMenuRightModal = true;
                this.menuFormData = bean;
            },
            // 登录
            login: function () {
                var that = this;
                Bmob.User.login(this.loginFormData.userName, this.loginFormData.passWord).then(res => {
                    // 登录成功
                    that.showLoginModal = false;
                    that.refresh();
                }).catch(err => {
                    that.showError('登录失败');
                });
            },
            // 显示详情
            showDetail: function (bean) {
                this.currentTodo = bean;
                this.showDetailModal = true;
            },
            // 保存详情
            saveDetail: function () {
                this.showDetailModal = false;
                const query = Bmob.Query('TodoBean');
                query.set('id', this.currentTodo.objectId);
                query.set('title', this.currentTodo.title);
                query.set('desc', this.currentTodo.desc);
                // 保存状态
                var that = this;
                query.save().then(res => {
                    that.getTodoList();
                    console.log('更新内容成功');
                }).catch(err => {
                    that.getTodoList();
                })
            },
            // 勾选状态变化
            onMissionChanged: function (bean) {
                const query = Bmob.Query('TodoBean');
                query.set('id', bean.objectId);
                query.set('done', bean.done);
                // 保存状态
                var that = this;
                query.save().then(res => {
                    that.getTodoList();
                    console.log('更新内容成功');
                }).catch(err => {
                    that.getTodoList();
                });
                // 设置金额
                // 检查是否存在金额
                const accQuery = Bmob.Query("AccHistoryBean");
                accQuery.equalTo("name", "==", bean.title);
                var that = this;
                accQuery.find().then(res => {
                    if (res.length > 0) {
                        // 已存在
                        if (!bean.done) {
                            // 删除积累
                            that.deleteAcc(res[0]);
                        }
                    } else {
                        // 未存在
                        if (bean.done) {
                            // 新建
                            that.createAcc(bean);
                        }
                    }
                });
            },
            // 创建累计
            createAcc: function (bean) {
                const query = Bmob.Query('AccHistoryBean');
                query.set('name', bean.title);
                query.set('money', 0.1);
                // 保存状态
                var that = this;
                query.save().then(res => {
                    that.getTodoList();
                    console.log('创建积累成功');
                }).catch(err => {
                    that.getTodoList();
                })
            },
            // 删除累计
            deleteAcc: function (bean) {
                const query = Bmob.Query('AccHistoryBean');
                query.destroy(bean.objectId).then(res => {
                    console.log('删除积累成功');
                }).catch(err => {

                    console.log(err)
                })
            },
            // 创建todo
            createTodo: function () {
                // 检查参数
                if (this.strError(this.todoFormData.title)) {
                    this.showError('请输入内容');
                    return;
                }
                if (this.currentMenu == null) {
                    this.showError('请选择清单');
                    return;
                }
                // 创建
                var that = this;
                const query = Bmob.Query('TodoBean');
                query.set("parent", this.currentMenu.objectId);
                query.set("title", this.todoFormData.title);
                query.save().then(res => {
                    that.getTodoList();
                    // 清空表单
                    that.todoFormData.title = '';
                }).catch(err => {
                    that.getTodoList();
                })

            },
            // 刷新
            refresh: function () {
                this.getMenuList();
            },
            // 获取菜单列表
            getMenuList: function () {
                const query = Bmob.Query("TodoMenuBean");
                query.order("-createdAt");
                var that = this;
                query.find().then(res => {
                    that.menus = res;
                    that.currentMenu = that.menus[0];
                    that.getTodoList();
                    console.log('菜单列表大小:' + that.menus.length)
                });
            },
            // 创建菜单
            createMenu: function () {
                // 检查参数
                if (this.strError(this.menuFormData.name)) {
                    this.showError('必须输入名称');
                    return;
                }
                var that = this;
                const query = Bmob.Query('TodoMenuBean');
                query.set("name", this.menuFormData.name);
                query.save().then(res => {
                    that.isNewMenu = false;
                    that.refresh();
                    that.menuFormData.name = '';
                }).catch(err => {
                    that.isNewMenu = false;
                    that.refresh();
                })
            },
            // 选中了清单
            handleClickMenu: function (bean) {
                // 根据清单获取任务列表
                this.currentMenu = bean;
                console.log('当前选中了:' + this.currentMenu.name);
                // 获取
                this.getTodoList();
            },
            // 获取todo列表
            getTodoList: function () {
                const query = Bmob.Query("TodoBean");
                query.equalTo("parent", "==", this.currentMenu.objectId);
                // 是否只显示以为完成
                if (this.onlyShowDone) {
                    query.equalTo("done", "==", false);
                }
                // 顺序
                query.order("done","-updatedAt");
                var that = this;
                query.find().then(res => {
                    that.todoList = res;
                    console.log('todo列表大小:' + that.todoList.length)
                });
            }
        }
    }
</script>

<style scoped>
    .left_menu {
        position: absolute;
        left: 0px;
        width: 200px;
        top: 0px;
        bottom: 0;
        height: 100%;
    }

    .delete_line {
        text-decoration: line-through;
        color: ghostwhite;
    }


    .todo_list {
        position: absolute;
        left: 200px;
        right: 0px;
        bottom: 0px;
        top: 0px;
        overflow: scroll;
        background-color: #545c64;
    }

    .el-checkbox {
        margin-right: 5px !important;
    }

    .el-divider--horizontal {
        margin: 0 !important;
    }

</style>
