import Vue from 'vue'
import Router from 'vue-router'
import MissionHome from './components/mission/Home.vue'

Vue.use(Router);

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: '首页',
            redirect: '/mission',
        },
        {
            path: '/mission',
            name: '任务',
            component: MissionHome
        }
    ]
})
