import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './registerServiceWorker'

Vue.config.productionTip = false

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);


// 工具类
// @ts-ignore
import MenuRouter from './utils/MenuRouter';
// @ts-ignore
import FormValidate from './utils/Validater';

Vue.prototype.MenuRouter = MenuRouter;
Vue.prototype.FormValidate = FormValidate;
Vue.use(MenuRouter);
Vue.use(FormValidate);

// iView
import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);

// Bmob
// @ts-ignore
import Bmob from "hydrogen-js-sdk";
Bmob.initialize("a635b131ea9961e1aa69084a5781c028", "66539b836e9073d839b7b5c0019c3951");
Vue.prototype.Bmob = Bmob;

// 右键菜单
// @ts-ignore
import mouseMenu from 'vue-mouse-menu'

Vue.use(mouseMenu);

new Vue({
    router,
    render: h => h(App)
}).$mount('#app');
